<script>
import moment from 'moment';
export default {
  name: 'NavBar', // 
  data() {
    return {
      time: true,
      month: '',
      year: ''
    };
  },
  beforeMount() {},
  props: {},
  created() {
    var self = this;
    self.month = parseInt(moment().format('M'));
    self.time = self.month > 9 || self.month < 5;
    self.year = moment().format('YYYY');
  }
};
</script>

