}
.ivu-layout-header {
  width: 100%;
  height: 80px;
  padding: 0;
  position: relative;
  line-height: 80px;
  z-index: 100;
  background: transparent;
}
.ivu-menu-horizontal {
  padding: 0 100px;
}
.ivu-menu-horizontal {
  min-width: 1425px;
  height: 80px;
  line-height: 80px;
}
.headerBox {
  height: 0px;
}
.headerBox.fixedBox {
  height: 110px;
}
.fixed {
  position: fixed;
  top: 30px;
}
</style>



