        .project-content {
          font-size: 14px;
          color: #333;
        }
        .project-link {
          font-size: 14px;
          text-align: right;
        }
      }
      .project-thumb {
        float: right;
        width: 350px;
        margin-right: -400px;
        margin-left: 50px;
      }
    }
    .project-item:first-child {
      margin-top: 0;
      padding-top: 0;
      border-top: none;
    }
    .project-item.thumb {
      padding-right: 400px;
    }
    .project-item:after {
      display: block;
      clear: both;
      content: '';
      visibility: hidden;
      height: 0;
    }
  }
  .project-not-found {
    line-height: 100px;
    height: 100px;
    text-align: center;
  }
}
</style>
<style lang="less">
.project-content {
  li {
    list-style: disc;
    margin-left: 1.3em;
  }
}
</style>



